
import "components/common/features.css";
import LinkNode from "components/links/LinkNode.vue";
import { StyleValue, Visibility } from "features/feature";
import { Resource } from "features/resources/resource";
import { PersistentRef } from "game/persistence";
import player from "game/player";
import { formatWhole } from "util/bignum";
import { processedPropType } from "util/vue";
import { computed, defineComponent, PropType, Ref, toRefs, unref } from "vue";

export default defineComponent({
    props: {
        visibility: {
            type: processedPropType<Visibility>(Number),
            required: true
        },
        style: processedPropType<StyleValue>(String, Object, Array),
        classes: processedPropType<Record<string, boolean>>(Object),
        xp: {
            type: Object as PropType<Resource>,
            required: true
        },
        level: {
            type: Object as PropType<Resource>,
            required: true
        },
        levelProgress: {
            type: Object as PropType<Ref<number>>,
            required: true
        },
        timeLoopActive: {
            type: Object as PropType<PersistentRef<boolean>>,
            required: true
        },
        name: {
            type: String,
            required: true
        },
        color: {
            type: processedPropType<string>(String),
            required: true
        },
        image: {
            type: processedPropType<string>(String),
            required: true
        },
        imageFocus: {
            type: processedPropType<{ x: string; y: string }>(Object),
            required: true
        },
        resource: Object as PropType<Resource>,
        layerID: {
            type: String,
            required: true
        },
        id: {
            type: String,
            required: true
        }
    },
    components: {
        LinkNode
    },
    setup(props) {
        const { timeLoopActive, layerID } = toRefs(props);

        const selected = computed(() => player.tabs.includes(layerID.value));
        const finishedFirstChapter = computed(() => player.layers.main.chapter !== 1);

        function openJob() {
            player.tabs.splice(1, 1, layerID.value);
        }

        function toggleLoop() {
            timeLoopActive.value = !timeLoopActive.value;
        }

        return {
            selected,
            finishedFirstChapter,
            hasTimeSlotAvailable: player.layers.main.hasTimeSlotAvailable as Ref<boolean>,
            formatWhole,
            openJob,
            toggleLoop,
            unref,
            Visibility
        };
    }
});
